/*global kakao*/
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { useRef, useState } from 'react'
import Modal from 'react-modal/lib/components/Modal'
import FmdGoodOutlinedIcon from '@mui/icons-material/FmdGoodOutlined';
import { IconButton } from '@mui/material';

const validColor = "#2BAE66";

function SelectLoc({ loc, setLoc, text, activate, setActivate }) {
    const [open, setOpen] = useState(false);
    const mapRef = useRef(null);

    const waitModalRender = (cb) => {
        if (mapRef.current === null)
            setTimeout(() => waitModalRender(cb), 0);
        else
            cb();
    }

    const iconClickHandler = (e) => {
        e.preventDefault();
        setOpen(true);

        waitModalRender(() => {
            const drawMap = (locc) => {
                const options = { //     
                    center: new kakao.maps.LatLng(locc.latitude, locc.longitude), // .
                    level: 3 // (,  )
                };
                const map = new kakao.maps.Map(mapRef.current, options); //    

                if (activate) {
                    const marker = new kakao.maps.Marker({
                        //     
                        position: new kakao.maps.LatLng(locc.latitude, locc.longitude),
                    });
                    marker.setMap(map);

                    const customOverlay = new kakao.maps.CustomOverlay({
                        map: map,
                        position: new kakao.maps.LatLng(locc.latitude, locc.longitude),
                        content: `<div style="padding:3px;position: relative;bottom:55px;color:black;background-color:white;border-radius:5px;border:1px solid black">${text}</div>`,
                    });
                }

                kakao.maps.event.addListener(map, 'click', (event) => {
                    //  ,    
                    const latlng = event.latLng;
                    setLoc({ latitude: latlng.getLat(), longitude: latlng.getLng() })
                    setOpen(false);
                    setActivate(true);
                });
            }
            drawMap(loc)
        })
    }
    return (
        <>
            <IconButton sx={{ "svg.MuiSvgIcon-root": { color: activate ? validColor : 'red' } }} size="large" onClick={iconClickHandler}>
                <FmdGoodOutlinedIcon fontSize="inherit" />
            </IconButton>
            <Modal
                isOpen={open}
                onRequestClose={() => setOpen(false)}
                ariaHideApp={false}
            >
                <div ref={mapRef} css={css`height:100%`}>

                </div>

            </Modal>
        </>
    )
}

export default SelectLoc