/*global kakao*/
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import { Button } from '@mui/material';
import { useEffect, useRef, useState } from 'react';
import Modal from 'react-modal/lib/components/Modal';
import { getDistance } from '../util/getDistance';
import { getLevelByDisance } from '../util/getLevelByDistance';
import { getMiddleLoc } from '../util/getMiddleLoc';
import { useMediaQuery } from 'react-responsive';

function ChatMapInfo({ room }) {
    const [open, setOpen] = useState(false);
    const mapRef = useRef(null);

    const waitModalRender = (cb) => {
        if (mapRef.current === null)
            setTimeout(() => waitModalRender(cb), 0);
        else
            cb();
    }

    const clickHandler = (e) => {
        setOpen(true);

        waitModalRender(() => {
            const startLoc = room.startLoc;
            const endLoc = room.endLoc;
            const middleLoc = getMiddleLoc(startLoc, endLoc);
            const options = { //     
                center: new kakao.maps.LatLng(middleLoc.latitude, middleLoc.longitude), // .
                level: getLevelByDisance(getDistance(startLoc.latitude, startLoc.longitude, endLoc.latitude, endLoc.longitude) * 1000) // (,  )
            };

            const map = new kakao.maps.Map(mapRef.current, options); //    

            // 
            const startMarker = new kakao.maps.Marker({
                position: new kakao.maps.LatLng(startLoc.latitude, startLoc.longitude)
            });
            startMarker.setMap(map);

            const startCustomOverlay = new kakao.maps.CustomOverlay({
                map: map,
                position: new kakao.maps.LatLng(startLoc.latitude, startLoc.longitude),
                content: `<div style="padding:3px;position: relative;bottom:55px;color:black;background-color:white;border-radius:5px;border:1px solid black">: ${room.start}</div>`,
            });

            // 
            const endMarker = new kakao.maps.Marker({
                position: new kakao.maps.LatLng(endLoc.latitude, endLoc.longitude)
            });
            endMarker.setMap(map);

            const endCustomOverlay = new kakao.maps.CustomOverlay({
                map: map,
                position: new kakao.maps.LatLng(endLoc.latitude, endLoc.longitude),
                content: `<div style="padding:3px;position: relative;bottom:55px;color:black;background-color:white;border-radius:5px;border:1px solid black">: ${room.end}</div>`,
            });
        })
    }

    const isPC = useMediaQuery({ query: "(min-width: 700px)" });

    const mapButtonSytle = {
        color: 'white',
        boxShadow: "0 0 0",
        backgroundColor: "#2BAE66",
        padding: "0px",
        fontSize: '0.8em',
        width: isPC ? '55px' : '80px',
        minWidth: '55px',

        "&:hover": {
            boxShadow: "0 0 0",
            backgroundColor: "#2BAE66"
        }
    };

    return (
        <>
            <Button sx={mapButtonSytle} onClick={clickHandler}></Button>
            <Modal
                style={{ overlay: { zIndex: 2000 } }}
                isOpen={open}
                onRequestClose={() => setOpen(false)}
                ariaHideApp={false}
            >
                <div ref={mapRef} css={css`height: 100%`}></div>
            </Modal>
        </>
    )
}

export default ChatMapInfo